.search-box {
  position: relative;
  width: 250px;
}
.search-box__input {
  outline: none;
  background: rgba(0, 0, 0, 0.85);
  border: solid 1px rgba(255, 255, 255, 0.85);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 7px 14px 7px 40px;
  width: 0%;
  font-size: 13px;
  color: #fff;
  opacity: 0;
  -webkit-transition: ease width 1s, ease opacity 1s;
  -o-transition: ease width 1s, ease opacity 1s;
  transition: ease width 1s, ease opacity 1s;
  float: right;
}
.search-box__icon {
  position: absolute;
  top: 9px;
  color: white;
  z-index: 10;
  cursor: pointer;
  right: 12px;
  -webkit-transition: ease transform 1s;
  -o-transition: ease transform 1s;
  transition: ease transform 1s;
  -webkit-transition-delay: 50ms;
       -o-transition-delay: 50ms;
          transition-delay: 50ms;
}
.search-box__close {
  position: absolute;
  top: 9px;
  color: white;
  z-index: 10;
  cursor: pointer;
  right: 12px;
  opacity: 0;
}
.search-box.active .search-box__icon {
  -webkit-transform: translateX(-214px);
      -ms-transform: translateX(-214px);
          transform: translateX(-214px);
}
.search-box.active .search-box__input {
  opacity: 1;
  width: 100%;
}
.search-box.active .search-box__close.active {
  opacity: 1;
}



